<script>
  import { onMount } from 'svelte';
  import { tick } from 'svelte';
  export let tab;

  // Component map dưới dạng dynamic import
  const componentMap = {
    user: () => import('./UserDetail.svelte'),
    report: () => import('./ReportView.svelte'),
    product: () => import('./ProductDetail.svelte'),
    result: () => import('./ResultDetail.svelte')
  };

  let loadedComponents = new Map();
  let currentComponent = null;
  let isLoading = false;

  // Load component khi tab thay đổi
  $: if (tab?.type) {
    loadComponent(tab.type);
  }

  async function loadComponent(type) {
    isLoading = true;

    // Nếu component đã load trước đó thì dùng lại
    if (loadedComponents.has(type)) {
      currentComponent = loadedComponents.get(type);
    } else {
      const module = await componentMap[type]();
      currentComponent = module.default;
      loadedComponents.set(type, currentComponent);
    }

    isLoading = false;
    await tick(); // đảm bảo DOM cập nhật sau khi thay đổi
  }
</script>

{#if isLoading}
  <div class="p-4 text-gray-400 italic">⏳ Loading tab content...</div>
{:else if currentComponent}
  <svelte:component this={currentComponent} {tab} />
{/if}
